import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Chip from '@material-ui/core/Chip';
import CancelIcon from '@material-ui/icons/Cancel';
import Typography from '@material-ui/core/Typography';

const useStyles = makeStyles((theme) => ({
  modal:{
    width:'100%',
    marginLeft: 'auto',
    marginRight: 'auto',
    padding:'1em',
    outline: 0, // add / remove
    maxHeight: '100%',
    overflowY: 'auto',
    '& > *': {
      width:'90%',
      marginTop: 0,
      marginLeft: 'auto',
      marginRight: 'auto',
      borderRadius: theme.spacing(1),
      align: 'center',
    },
  },
  textArea:{
    marginLeft: 'auto',
    marginRight: 'auto',
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(2),
    width:'90%',
    height:'30%',
    whiteSpace:'nowrap',
    overflow:'scroll',
    overflowY: 'scroll',
    overflowX: 'scroll',
    resize: 'none',
  },
  tagsArea:{
    width:'90%',
    marginTop: theme.spacing(2),
    display: 'flex',
    flexWrap: 'wrap',
    flexDirection: 'row',
    '& > *': {
      marginLeft: theme.spacing(1),
      marginRight: theme.spacing(1),
    },
  },
  IconArea:{
    display: 'flex',
    flexWrap: 'no-wrap',
    flexDirection: 'row',
    justifyContent: 'space-between',
    width:'90%',
    marginTop: theme.spacing(2),
  },
  cancelIcon:{
    '&:hover': {
      transform: 'scale(1.1)',
      background: 'rgba(50, 50, 50, 0.08)',
      borderRadius: theme.spacing(50),
    },
  }
}));
export default function AsciiCardModal(props) {
  const classes = useStyles();

  return (
    <Modal
      open={props.modalOpen}
      onClose={props.onClose}
      aria-labelledby="simple-modal-title"
      aria-describedby="simple-modal-description"
    >
      <div className={classes.modal}>
        <Paper elevation={4}>
          <Grid
            container
            direction="column"
            justify="center"
            alignItems="center"
          >
          <div className={classes.IconArea}>
            <Typography variant="h4" >
              {props.title} 
            </Typography> 
            <CancelIcon fontSize="large" onClick={props.onClose} className={classes.cancelIcon}/>
          </div>
          <div className={classes.tagsArea}>
            { props.tags.map(function (tag, index) {
                return <Chip label={tag} key={index} />
              }) 
            }
          </div>
          <textarea 
            rows={40} 
            cols={props.asciiWidth} 
            placeholder={""} 
            className={classes.textArea} 
            value = {props.context}
            onChange={()=>{}}
          /> 
          </Grid>
        </Paper>
      </div>
    </Modal>
    
  );
}