/* eslint-disable no-use-before-define */
import React from 'react';
import Chip from '@material-ui/core/Chip';
import Autocomplete from '@material-ui/lab/Autocomplete';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Paper from '@material-ui/core/Paper';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import DirectionsIcon from '@material-ui/icons/Directions';
import Tooltip from '@material-ui/core/Tooltip';
import SearchIcon from '@material-ui/icons/Search';
import {Link} from 'react-router-dom';
const useStyles = makeStyles((theme) => ({
  root: {
    padding: '2px 4px',
    display: 'flex',
    alignItems: 'center',
    minWidth: theme.spacing(40),
    maxWidth: theme.spacing(80),
  },
  input: {
    marginLeft: theme.spacing(1),
    flex: 1,
  },
  iconButton: {
    padding: 10,
  },
  divider: {
    height: 28,
    margin: 4,
  },
}));

export default function SearchAutocomplete(props) {
  const classes = useStyles();

  return (
 
    <Paper  >
      <form onSubmit={props.handleSubmit} className={classes.root}>
        <Autocomplete
          className={classes.input}
          multiple
          id="tags-filled"
          options={props.autoWords.map((option) => option.name)}
          defaultValue={[props.autoWords[0].name] }
          freeSolo
          renderTags={(value, getTagProps) =>
            value.map((option, index) => (
              <Chip variant="outlined" label={option} {...getTagProps({ index })} />
            ))
          }
          renderInput={(params) => (
            <TextField {...params}  label="Search by tag" />
          )}
          onChange={props.onChange}
        />

        <IconButton type="submit" className={classes.iconButton} aria-label="search">
          <SearchIcon />
        </IconButton>
        <Divider className={classes.divider} orientation="vertical" />
        <Tooltip title={props.directionTitle}>
          <IconButton color="primary" className={classes.iconButton} aria-label="directions" component={Link} 
          to={props.directTo}>
          <DirectionsIcon />
          </IconButton>
        </Tooltip>
    
      </form>
    </Paper>
  );
}